import { Controller, ContextMenuItem } from '@ali/4ever-cangjie';
import { CommentRole } from '../role';
import { ACTION_COMMENT_CLICK } from '../actions';
import { CommentPluginConfigs } from '../types';
import canComment from '../queries/canComment';

export default (configs: CommentPluginConfigs | undefined) => {
  return function contextMenu(controller: Controller, next) {
    if (!canComment(controller)) {
      return next();
    }
    const isDisabled = configs?.isDisabled && configs.isDisabled();
    const title = configs?.locale?.comment || 'Comment';
    const item: ContextMenuItem = {
      name: title,
      action: {
        type: ACTION_COMMENT_CLICK,
      },
      key: 'comment',
      icon: 'https://gw.alicdn.com/imgextra/i2/O1CN01GtHgGq1jGfO1gqxmG_!!6000000004521-55-tps-24-24.svg',
      options: {
        group: 100,
        mobile: true,
        hotKey: controller.readOnly ? '' : 'mod+shift+m',
        disable: isDisabled,
      },
      role: CommentRole.comment,
    };
    return [item, ...next()];
  };
};
